import React from 'react'
import { Link } from 'gatsby'
// import Tag from '../../components/Tag'

export default function PostCard ({ post }) {
	return (
		<div className='w-1/3 p-4'>
			<div className='overflow-hidden rounded shadow-lg'>
				<Link className='has-text-primary' to={post.fields.slug} key={post.id}>
					<img className='w-full' src={post.frontmatter.cover} alt={post.frontmatter.title} />
					<div className='px-6 py-4'>
						<div className='mb-2 text-xl font-bold'>{post.frontmatter.title}</div>
						<p className='text-base text-gray-700'>
							{post.frontmatter.meta_description}
						</p>
					</div>
					<div className='px-6 py-4'>
						{/* <img className="w-10 h-10 mr-4 rounded-full" src="/img/jonathan.jpg" alt="Avatar of Jonathan Reinink" /> */}
						<div className='text-sm'>
							<p className='leading-none text-gray-900'>{post.frontmatter.author}</p>
							<p className='text-gray-600'>{post.frontmatter.date}</p>
						</div>
					</div>
				</Link>
			</div>
		</div>
	)
}

