import React from 'react'
import { graphql } from 'gatsby'
import { kebabCase } from 'lodash'
import Layout from '../components/Layout'
// import Tag from '../components/Tag'
import PostCard from '../components/PostCard'

import { useSpring, animated } from 'react-spring'

const AnimFeTurbulence = animated('feTurbulence')
const AnimFeDisplacementMap = animated('feDisplacementMap')

export default ({
	data: {
		allMarkdownRemark: { edges },
	},
}) => {
	const { freq, scale, transform, opacity } = useSpring({
		from: { scale: 10, opacity: 0.3, transform: 'scale(0.9)', freq: '0.0175, 0.0' },
		to: { scale: 15, opacity: 1, transform: 'scale(1)', freq: '0.0, 0.0' },
		config: { duration: 3000 },
	})

	return (
		<Layout includePreFooter>
			<div className='py-48 text-6xl font-hairline text-center text-purple-100 bg-gradient-to-r from-yellow-400 via-red-500 to-indigo-500'>
				<animated.svg height={0}>
					<defs>
						<filter id='water'>
							<AnimFeTurbulence type='fractalNoise' baseFrequency={freq} numOctaves='1.5' result='TURB' seed='8' />
							<AnimFeDisplacementMap xChannelSelector='R' yChannelSelector='G' in='SourceGraphic' in2='TURB' result='DISP' scale={scale} />
						</filter>
					</defs>
				</animated.svg>
				<animated.p style={{ filter: 'url(#water)', transform: transform, opacity: opacity }}>
					Blog
				</animated.p>
			</div>

			<div className='container px-8 mx-auto'>
				<section className='flex flex-wrap my-12 mb-4'>
					{edges.map(edge => <PostCard key={edge.node.slug} post={edge.node} />)}
				</section>
			</div>

		</Layout>
	)
}

export const pageQuery = graphql`
  query {
    allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/.*blog.*/"}}, sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          frontmatter {
            slug
            title
            tags
						cover
						date(formatString: "MMMM DD, YYYY")
						meta_description
						author
          }
					fields {
						slug
					}
				}
			}
		}
	}
`
